.main {
	padding-top: 40px;
}

.content {
	display: flex;
	width: 450px;

	.left,
	.right {
		max-width: 50%;
		width: 100%;
	}

	.left {
		padding-right: 5px;
		display: flex;
		flex-direction: column;
		.list {
			gap: 10px;
			display: flex;
			flex-direction: column;
			margin-bottom: auto;

			.item {
				display: flex;

				span {
					display: block;
					max-width: 60%;
					width: 100%;
					color: #71727a;
					font-size: 10px;
					font-style: normal;
					font-weight: 400;
					line-height: normal;
				}

				strong {
					flex: 1;
					color: #1f2024;
					font-size: 10px;
					font-style: normal;
					font-weight: 700;
					line-height: normal;
				}
			}
		}
	}

	.right {
		img {
			width: 100%;
			height: 220px;
			object-fit: contain;
			object-position: center;
		}
	}
}
